<template>
  <!--个人信息设置-->
  <i-form :model="formValidate" :label-width="80" ref="formValidate" :rules="ruleValidate">
    <div class="user-setting">

    <i-form-item label="昵称： " prop="nickName">
      <i-input show-message v-model="formValidate.nickName" placeholder="请输入昵称" style="width: 250px"></i-input>
    </i-form-item>

      <i-form-item label="我的签名： ">
        <i-input style="width: 50%" v-model="formValidate.autograph"  type="textarea" :autosize="{minRows: 5,maxRows: 10}" placeholder="请输入签名..."></i-input>
      </i-form-item>

      <i-form-item label="性别： ">
        <i-radio-group v-model="formValidate.sex">
          <i-radio label="0">男</i-radio>
          <i-radio label="1">女</i-radio>
        </i-radio-group>
      </i-form-item>

      <i-form-item label="出生日期：" prop="birthday">
      <col>
      <i-date-picker :editable="editable" :value="formValidate.birthday" format="yyyy-MM-dd" type="date" placeholder="选择日期"
                      @on-change="getBirthday"></i-date-picker >
      </col>
      </i-form-item>



    <i-form-item label="所在地：" >
      <i-select v-model="pSelect"  placeholder="请选择一级地区" style="width: 200px" @on-change="select" label="pSelect">
      <i-option :value="item.name" v-for="item in city">{{item.name}}</i-option>
    </i-select>
      <select class="mySelect" v-model="cSelect"   style="width: 200px;margin-left: 15px" :value="cSelect" label="cSelect">
        <option class="myOption"  v-for="child in children" v-show="child.p == pSelect" @click="cChange" >{{child.name}}</option>
      </select>
    </i-form-item>



    <i-form-item style="margin-top: 50px">
      <i-button type="primary" style="width: 100px" @click="setUserInfo('formValidate')">保存</i-button>
    </i-form-item>
    </div>
  </i-form>
</template>
<script>
import IFormItem from 'iview/src/components/form/form-item.vue';
import IForm from 'iview/src/components/form/form.vue';
import IInput from 'iview/src/components/input/input.vue';
import ISelect from 'iview/src/components/select/select.vue';
import IOption from 'iview/src/components/select/option.vue';
import IDatePicker from 'iview/src/components/date-picker';
import IRadioGroup from 'iview/src/components/radio/radio-group.vue';
import IRadio from 'iview/src/components/radio/radio.vue';
import IButton from 'iview/src/components/button';
import IUpload from 'iview/src/components/upload';
import {getNickName} from '../common/js/url'

  export default {
    created(){
      getNickName().then((res) =>{
        this.formValidate.nickName = res.result.nickname
        this.formValidate.autograph = res.result.signature
        this.pSelect = res.result.area
        this.userId = res.result.usrId
        this.formValidate.sex = res.result.sex
        this.formValidate.birthday = res.result.birthday
        setTimeout(()=>{
          this.cSelect = res.result.region
        },20)
      })
    },
    data () {
      const validateNickName = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入昵称'));
        } else {
          callback();
        }
      };
      const validateBirthday = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请选择日期'));
        }else {
          callback();
        }
      };
      return {
        formValidate: {
          nickName: '',   //昵称
          sex: '',
          birthday: '',
          autograph: '',  //签名
        },
        ruleValidate: {
          nickName: [
            { validator: validateNickName, trigger: 'blur' }
          ],
          birthday:[
            { validator: validateBirthday, trigger: 'change'}
          ]
        },
        editable:false,  //日期不能输入
        pSelect:'',
        cSelect:'',
        userId:'',
        loadingStatus: false,
        city:[{name:"北京市"},
          {name:"上海市"},
      {name:"浙江省"},
          {name:"天津市"},
          {name:"重庆市"},
        ],
        children:[{name:"东城区",p:"北京市"},{name:"西城区",p:"北京市"},
          {name:"朝阳区",p:"北京市"},{name:"崇文区",p:"北京市"},{name:"海淀区",p:"北京市"},{name:"宣武区",p:"北京市"},
          {name:"石景山区",p:"北京市"},{name:"门头沟区",p:"北京市"},{name:"丰台区",p:"北京市"},{name:"房山区",p:"北京市"},
          {name:"大兴区",p:"北京市"},{name:"通州区",p:"北京市"},{name:"顺义区",p:"北京市"},{name:"平谷区",p:"北京市"},
          {name:"昌平区",p:"北京市"},{name:"怀柔区",p:"北京市"},{name:"延庆县",p:"北京市"},{name:"密云县",p:"北京市"},
          {name:"黄浦区",p:"上海市"},{name:"徐汇区",p:"上海市"},{name:"徐汇区",p:"上海市"},{name:"静安区",p:"上海市"},{name:"普陀区",p:"上海市"},
          {name:"虹口区",p:"上海市"},{name:"杨浦区",p:"上海市"},{name:"闵行区",p:"上海市"},{name:"宝山区",p:"上海市"},{name:"嘉定区",p:"上海市"},
          {name:"浦东新区",p:"上海市"},{name:"金山区",p:"上海市"},{name:"松江区",p:"上海市"},{name:"青浦区",p:"上海市"},{name:"奉贤区",p:"上海市"},
          {name:"杭州市",p:"浙江省"},{name:"宁波市",p:"浙江省"},{name:"温州市",p:"浙江省"},{name:"嘉兴市",p:"浙江省"},{name:"湖州市",p:"浙江省"},
          {name:"绍兴市",p:"浙江省"},{name:"金华市",p:"浙江省"},{name:"衢州市",p:"浙江省"},{name:"舟山市",p:"浙江省"},{name:"台州市 ",p:"浙江省"},{name:"丽水市 ",p:"浙江省"},
          {name:"和平区",p:"天津市"},{name:"河东区",p:"天津市"},{name:"河西区",p:"天津市"},{name:"南开区",p:"天津市"},{name:"河北区",p:"天津市"},
          {name:"红桥区",p:"天津市"},{name:"东丽区",p:"西青区"},{name:"津南区",p:"天津市"},{name:"北辰区",p:"天津市"},{name:"武清区",p:"天津市"},
          {name:"宝坻区",p:"天津市"},{name:"滨海新区",p:"西青区"},{name:"宁河区",p:"天津市"},{name:"静海区",p:"天津市"},
          {name:"万州区",p:"重庆市"},{name:"涪陵区",p:"重庆市"},{name:"渝中区",p:"重庆市"},{name:"大渡口区",p:"重庆市"},{name:"江北区",p:"重庆市"},
          {name:"沙坪坝区",p:"重庆市"},{name:"九龙坡区",p:"重庆市"},{name:"南岸区",p:"重庆市"},{name:"北碚区",p:"重庆市"},{name:"綦江区",p:"重庆市"},
          {name:"大足区",p:"重庆市"},{name:"渝北区",p:"重庆市"},{name:"巴南区",p:"重庆市"},{name:"黔江区",p:"重庆市"},{name:"长寿区",p:"重庆市"},
          {name:"江津区",p:"重庆市"},{name:"合川区",p:"重庆市"},{name:"永川区",p:"重庆市"},{name:"南川区",p:"重庆市"},{name:"璧山区",p:"重庆市"},
          {name:"铜梁区",p:"重庆市"},{name:"潼南区",p:"重庆市"},{name:"荣昌区",p:"重庆市"},]
      }
    },
    methods:{
      select(value){
        this.cSelect = ""
        this.pSelect = value
      },
      cChange(value){
        this.cSelect = value
      },
      getBirthday(time){
        this.formValidate.birthday = time
      },
      setUserInfo(name){
        var _this = this
        this.$refs[name].validate((valid) => {
          if (valid) {
            $.ajax({
              url: './setUserInfo' ,
              type: 'POST',
              data: `nickname=${this.formValidate.nickName}&signature=${this.formValidate.autograph}&sex=${this.formValidate.sex}&birthday=${this.formValidate.birthday}
              &area=${this.pSelect}&region=${this.cSelect}&usrId=${this.userId}`,
              dataType: "json",
              success: function (returndata) {
                console.log(returndata);
                console.log("success")
                _this.$Message.success("上传成功！");
                _this.$Message.config({
                  top: 50,
                  duration: 4
                });
              },
              error: function (XMLHttpRequest, textStatus, errorThrown) {
                console.log(XMLHttpRequest.status);
                console.log(XMLHttpRequest.readyState);
                console.log(textStatus);
                console.log("fail")
                _this.$Message.error("上传出错！");
                _this.$Message.config({
                  top: 50,
                  duration: 4
                });
              }
            });
          } else {
            this.$Message.error('请填写完整的信息!');
          }
        })
      },
    },
    components:{
      IFormItem,
      IForm,
      IInput,
      ISelect,
      IOption,
      IDatePicker,
      IRadioGroup,
      IRadio,
      IButton,
      IUpload,
    },
    deactivated(){
      this.$destroy(true)
    }
  }
</script>
<style>
  .user-setting{
    text-align: left;
  }
  .mySelect{
    height: 32px;
    position: relative;
    box-sizing: border-box;
    outline: none;
    user-select: none;
    cursor: pointer;
    background-color: #fff;
    border-radius: 4px;
    border: 1px solid #d7dde4;
    transition: all 0.2s ease-in-out;
    color: #657180;
  }
  .mySelect:hover{
    border-color: #5cadff ;
  }

</style>



